//  --------------------------------------------------------------------------------
//  Contains style that are specific for home page
//  --------------------------------------------------------------------------------
#app {

}

.game {
  height: calc(100vh - calc(100vh - 100%))
}

.board {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  @include respond(phone) {
    justify-content: unset;
  }
}

.game-header {
  width: 100%;
  height: 6rem;
  display: flex;
  justify-content: space-between;
  background: #1d1d20;
  &_box {
    display: flex;
  }
  &_box.left {
    button {
      margin: 1rem 0rem 1rem 0;
    }
  }
  &_box.right {
    button {
      margin: 1rem 0rem 1rem 0;
    }
  }
}

.tiles {

}

.tiles-word {
  display: flex;
  align-items: center;
  justify-content: center;
}

.tiles-word_letter {
  width: 5.2rem;
  height: 5.2rem;
  border: 0.3rem solid #2f2f2f;
  border-radius: 0.61rem;
  box-sizing: border-box;
  margin: 0.26rem;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  color: #fff;
  align-items: center;
  justify-content: center;
  font-weight: bolder;
  font-size: 2.8rem;
  position: relative;
  z-index: 3;
  background: #1d1d20;
  @include respond(phone) {
    width: 4.2rem;
    height: 4.2rem;
    margin: 0.2rem;
  }
  @include respond(tab-land) {
    width: 4.8rem;
    height: 4.8rem;
    margin: 0.2rem;
  }



}
.tiles-word_letter.active {
  border-color: #646464;
  transition: border .1s;
}
.tiles-word_letter.grey {
  background: #919191;
  animation-name: flip-horizontal-bottom;
  animation-duration: 250ms;
}
.tiles-word_letter.orange {
  background: #d3952a;
  animation-name: flip-horizontal-bottom;
  animation-duration: 250ms;
  & div {
  }
}
.tiles-word_letter.green {
  background: limegreen;
  animation-name: flip-horizontal-bottom;
  animation-duration: 250ms;
}


@keyframes flip-horizontal-bottom{
  0%{
    -webkit-transform:rotateX(0);
    transform:rotateX(0)}
  100%{
    -webkit-transform:rotateX(-90deg);transform:rotateX(-90deg)
  }
}

.keyboard {
  margin: 2rem 1rem;
}

.keyboard-row {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 0.5rem;
}

.keyboard-row_letter {
  width: 4rem;
  height: 5.8rem;
  @include respond(phone) {
    width: 3.4rem;
  }
  @include respond(tab-port) {
    width: 3.8rem;
  }
  cursor: pointer;
  margin: 0.2rem;
  padding: 0;
  font-size: 1.8rem;
  font-weight: 700;
  color: #fff;
  background: #636363;
  border-radius: 0.4rem;
  border: none;
  border-bottom: 0.2rem solid #4d4d4d;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
}

.keyboard-row_letter.big {
  width: 4rem;
}

.keyboard-row_letter.correct {
  background: limegreen;
}

.keyboard-row_letter.found {
  background: #d3952a;
}

.keyboard-row_letter.wrong {
  background: #919191;
}
